// Mobile: max 500px
@media only screen and (max-width: $mobile-middle-width) {
  .block-newsletter {
    .wrapper {
      &__bg-wrap,
      .bg-wrap {
        padding: 32px 2% 45px;
      }
    }
  }
}

// Mobile: max 322px
@media only screen and (max-width: $mobile-min-width) {
  .block-newsletter {
    .wrapper {
      &__bg-wrap,
      .bg-wrap {
        &__subscribe-wrap,
        .subscribe-wrap {
          display: flex;
          flex-wrap: wrap;
          &__input {
            border-radius: 4px 4px 0 0;
            max-width: 250px;
            width: 100%;
          }

          &__accent-btn,
          .accent-btn {
            border-radius: 0 0 4px 4px;
            max-width: 252px;
            width: 100%;
          }
        }
      }
    }
  }
}
