.block-stats {
  @include section-wrap(100%);

  // align-items: center;
  // margin: 0 auto;

  .wrapper {
    // align-items: center;
    background-color: $bg-stats;
    border-radius: 8px;
    &__wrap,
    .wrap {
      @include flex(row, wrap, space-between, center);
      width: 100%;
      margin-top: 36px;
      margin-bottom: 42px;

      &__item,
      .item {
        &__head {
          @include font(24px, 700, 29px, $text-main, left);
        }

        &__desc {
          @include font(16px, 500, 19px, $text-main, left);
        }
      }
      &__item--first {
        margin-left: 64px;
        padding-left: 0;
      }

      &__item--last {
        margin-right: 64px;
      }
    }
  }
}

// Tablet: max 1440px
@media only screen and (max-width: $table-big-width) {
  .block-stats {
    // @include section-wrap(100%);
  }
}

// Tablet: max 1024px
@media only screen and (max-width: $table-width) {
  .block-stats {
    // @include section-wrap(100%);
    background-color: $bg-stats;
    border-radius: none;
  }
}

